// Styleguide Components.Blocks.DefaultSearchForm
.block--search-form {
  @include font-size(13px);

  line-height: 1.5em;

  float:    right;
  position: relative;
  top:      0.8em;
  top:      1rem;

  .form-text {
    width:  2em;
    height: 2em;

    margin:  0;
    padding: 0 1em;

    border:        1px solid transparent;
    border-radius: 2em;
    background:    transparent
                   url(../../images/icons/Search.png)
                   right center
                   no-repeat;
    transition:    all ease 0.25s;

    &:focus {
      width:  15em;
      border: 1px solid #262626;
      border: 1px solid rgba(0, 0, 0, 0.15);

      box-shadow:       #06f 0 0 7px;
      background-color: $color-white;
    }
  }

  .form-submit,
  input[type="submit"] {
    display: none;
    cursor:  pointer;
  }
}

.region--navigation .block--search-form {
  @include media($tablet) {
    display: none;
  }
}
